<template>
  <div>
    <!--/:appName/:bundle/:service + http method + rule-->
    <Row>
      <Col span="6">
      URL: &nbsp;&nbsp;{{showUrl}}
      </Col>

      <Col span="4">
      | rule: {{ruleLabel}}
      <!--
      <Select v-model="ruleId">
        <Option v-for="item in ruleList" :value="item.value" :key="item">{{ item.label }}</Option>
      </Select>
      -->
      </Col>

      <Col span="4">
      &nbsp;&nbsp;&nbsp;<a @click="genParams">gen entity params</a>
      </Col>

      <Col span="10" v-show="httpMethod != 'post'">
      &nbsp;&nbsp;&nbsp;Condition exp: {{currentRuleCondition}}
      </Col>

    </Row>

    <Row>
      <Col span="12">
      Entity
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div id="paramArea"
           style="background-color: #fff; margin: 10px 10px 10px 10px;min-height: 250px; border: 1px solid #ccc">
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="12">
      Condition
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div id="conditionArea"
           style="background-color: #fff; margin: 10px 10px 10px 10px;min-height: 250px; border: 1px solid #ccc">
      </div>
      </Col>
    </Row>

    <div style="margin-top: 15px">
      <Row>
        <Col span="8">
        &nbsp;
        </Col>
        <Col span="8">
        <Button type="primary" long @click="executeApi">Execute</Button>
        </Col>
        <Col span="4">
        &nbsp;
        </Col>
        <Col span="4">

        </Col>
      </Row>
    </div>
    <div>
      <pre>{{executeResult}}</pre>
    </div>
  </div>
</template>
<script>
  import {BundleDS} from './bundleDs'

  import {DataSource} from '../../utils/dataSource';

  import {EntityDS} from './entityDs';

  import {DS} from './tableDs';


  var ace = require('brace');
  require('brace/mode/json');


  export default {
    created() {
      this.fetchData();
    },
    watch: {
      '$route': 'fetchData'
    },
    data () {
      return {
        ruleId: '',
        ruleLabel: '',
        showUrl: '',
        httpMethod: '',
        rules: [],
        ruleList: [],
        ruleMap: {},
        example: {},
        executeResult: ''
      }
    },
    mounted(){
      var editor = ace.edit('paramArea')
      editor.getSession().setMode('ace/mode/json')
      this.$paramArea = editor
      this.$paramArea.clearSelection()

      var editor2 = ace.edit('conditionArea')
      editor2.getSession().setMode('ace/mode/json')
      this.$conditionArea = editor2
      this.$conditionArea.clearSelection()

    },

    computed: {
      currentRuleCondition() {
        if (this.ruleId && this.ruleMap[this.ruleId]) {
          return this.ruleMap[this.ruleId].cond.expression
        }
        return "";
      }
    },

    methods: {
      genParams() {
        this.$paramArea.setValue(JSON.stringify(this.example, null, 4))
        this.$paramArea.clearSelection()
      },
      executeApi(){
        var paramsValue = this.$paramArea.getValue();
        var cndValue = this.$conditionArea.getValue();
        if (paramsValue == '') {
          paramsValue = '{}';
        }
        if (cndValue == '') {
          cndValue = '{}';
        }
        console.log(paramsValue);
        var params = JSON.parse(paramsValue);
        var cnd = JSON.parse(cndValue);
        cnd['_mock_rule_value_'] = this.ruleMap[this.ruleId].name;
        var result;
        if (this.httpMethod == 'get') {
          result = this.$ds.paged(cnd)
        } else if (this.httpMethod == 'post') {
          result = this.$ds.create(params, {'_mock_rule_value_': this.ruleMap[this.ruleId].name})
        } else if (this.httpMethod == 'put') {
          result = this.$ds.update(params, cnd)
        } else if (this.httpMethod == 'delete') {
          result = this.$ds.remove(cnd);
        }
        var $this = this;
        result.then(function (data) {
          return data.json();
        }).then(function (data) {
          $this.executeResult = JSON.stringify(data, null, 4);
        });
      },

      fetchData() {
        var id = this.$route.params.id;
        var ruleid = this.$route.params.ruleId;

        var $this = this;
        BundleDS.findById(id).then(function (data) {
          var appName = data.appId$display$;
          var bundleName = data.pid$display$;
          var serviceName = data.name;
          var entityAlias = data.entity.mainAlias;
          var method = data.method;
          var rules = data.rules;
          $this.httpMethod = method;
          $this.rules = rules;
          //_mock_rule = rule
          var url = '/rest/' + appName + '/' + bundleName + '/' + serviceName + '?_mock_authed=true';
          var showUrl = '/rest/' + appName + '/' + bundleName + '/' + serviceName + "?_method=" + method;
          $this.showUrl = showUrl;
          var ds = new DataSource({
            name: data.entity.name,
            createUrl: url,
            updateUrl: url,
            queryUrl: url,
            deleteUrl: url
          })
          $this.$ds = ds;


          var ruleList = [];
          if (rules) {
            rules.forEach(function (o) {
              ruleList.push({
                value: o.id,
                label: o.name
              })
              $this.ruleMap[o.id] = o;
              if (o.id == ruleid) {
                $this.ruleLabel = o.name
              }
            })
          }
          $this.ruleList = ruleList;

          $this.ruleId = ruleid;


          var example = {};

          EntityDS.findById(data.entityId).then(function (data) {
            if (data.rels) {
              data.rels.forEach(function (rel) {
                if (rel.type == 'ref') {
                  example[rel.alias] = [];
                }
              })
            }
            var mainTableId = data.mainTableId
            DS.findById(mainTableId).then(function (data) {
              var columns = data.columns;
              columns.forEach(function (o) {
                example[o.name] = '';
              })
              $this.example = example;
            });
          });


        });


      }
    }
  }
</script>



