<template>
  <div>
    <Tree ref="tree" :data="baseData" :multiple="multiple" :show-checkbox="showCheckbox"></Tree>

    <div style="margin-top: 15px">
      <Row>
        <Col span="8">
        &nbsp;
        </Col>
        <Col span="8">
        <Button type="primary" long icon="plus-round" @click="savePermission">Save</Button>
        </Col>
        <Col span="8">

        </Col>
      </Row>
    </div>

  </div>
</template>
<script type="text/ecmascript-6">
  import {DataSource} from '../../utils/dataSource';
  import {ApiPermissionDS} from './apiPermissionDs';
  var ds = new DataSource({
    queryUrl: '/rest/configServer/access/bundle'
  });


  export default {
    created() {
      this.fetchData();
    },
    watch: {
      '$route': 'fetchData'
    },
    data () {
      return {
        baseData: [],
        multiple: true,
        showCheckbox: true,
        nodeMap: {}
      }
    },
    methods: {
      fetchData(){

        var $this = this;
        var appId = this.$route.params.appId;
        var ruleId = this.$route.params.ruleId;

        ApiPermissionDS.query(appId).then(function (api) {
          console.log(api)
          var pathMap = {};
          api.forEach(function (item) {
            if (item.type == 'service') {
//              debugger;
              if (item.rules) {
                item.rules.forEach(function (rule) {
                  var path = item.bundle + "/" + item.service + "/" + item.method + "/" + rule.rule;
                  console.log(path);
                  pathMap[path] = rule;
                });
              }
            }
          });
          ds.paged({
            appId: appId
          }).then(function (data) {
            return data.json()
          }).then(function (data) {
            console.log(data.result);

            data.result.forEach(function (item) {
              if (item.type == 'service') {
                item.rules.forEach(function (rule) {
                  //var path = item.bundle + "/" + item.service + "/" + item.method + "/" + rule.rule;
                  var path = item['pid$display$'] + '/' + item.name + "/" + item.method + "/" + rule.name;
                  console.log(path);
                  if (pathMap[path] != undefined) {
                    rule.selected = true;
                  }
                  rule.path = path;
                })
              }
            });

            var bundles = [];

            function append(pid, root, value) {
              for (var i = 0; i < root.length; i++) {
                if (root[i].id == pid) {
                  if (root[i].children == undefined) {
                    root[i].children = [];
                  }
                  root[i].children.push(value);
                  break;
                }
                if (root.children != undefined) {
                  append(pid, root.children, value);
                }
              }
            }

            function convertObject(o) {
              var items = [];
              if (o.rules) {
                o.rules.forEach(function (element) {
                  var title = "<span>" + element.name + "&nbsp;</span>";
                  var object = {
                    serviceId: o.id,
                    id: element.id,
                    expand: true,
                    title: title,
                    type: 'rule',
                    selected: false,
                    checked: element.selected,
                    source: element
                  }
                  if (ruleId && ruleId == object.id) {
                    object.selected = true;
                  }
                  items.push(object);
                });
              }
              var title = o.name + ":" + o.method;
              var expand = true;
              if (o.type == 'dir') {
                title = "<span><i class='ivu-icon ivu-icon-ios-folder-outline'></i>&nbsp" + o.name + "</span>";
              } else if (o.type == 'service') {
                title = "<span> &nbsp;<i class='ivu-icon ivu-icon-steam'></i> " + o.name + ":" + o.method + "&nbsp;&nbsp;</span>";
                expand = true;
              }
              return {
                id: o.id,
                expand: expand,
                title: title,
                type: o.type,
                children: items,
                source: o
              }
            }

            for (var i = 0; i < data.result.length; i++) {
              var value = data.result[i];
              $this.nodeMap[value.id] = value;
              if (value.pid == undefined) {
                bundles.push(convertObject(value))
              }
            }
            for (var i = 0; i < data.result.length; i++) {
              var value = data.result[i];
              if (value.pid == undefined) {
              } else {
                append(value.pid, bundles, convertObject(value))
              }
            }

            $this.baseData = bundles;

          })
        });


      },
      savePermission() {
//        console.log(this.$refs.tree.getCheckedNodes());
        var appId = this.$route.params.appId;

        var datas = {};
        var checkNodes = this.$refs.tree.getCheckedNodes();
        console.log(checkNodes);
        var $this = this;
        checkNodes.forEach(function (item) {
          var type = item.type;
          if (type == 'rule') {
            var serviceId = item.serviceId;
            var dirId = $this.nodeMap[serviceId].pid;
            if (datas[dirId] == undefined) {
              //dir
              datas[dirId] = JSON.parse(JSON.stringify($this.nodeMap[dirId]));
            }
            if (datas[dirId].children == undefined) {
              datas[dirId].children = {};
            }
            if (datas[dirId].children[serviceId] == undefined) {
              datas[dirId].children[serviceId] = JSON.parse(JSON.stringify($this.nodeMap[serviceId]));
              datas[dirId].children[serviceId].rules = [];
            }
            datas[dirId].children[serviceId].rules.push(item);
            //JSON.parse(JSON.stringify($this.nodeMap[serviceId]));
          }
        });
//        console.log("datas=" + JSON.stringify(datas));
        var array = [];
        for (var key in datas) {
          var dir = datas[key];
          var dirObject = {
            appId: dir.appId,
            bundle: dir.name,
            type: 'bundle',
            children: []
          };
          for (var sKey in dir.children) {
            var service = dir.children[sKey];
            var serviceObject = {
              appId: service.appId,
              bundle: service.pid$display$,
              service: service.name,
              type: 'service',
              method: service.method,
              entityId: service.entity.id,
              rules: []
            };
            service.rules.forEach(function (rule) {
              var ruleObject = {
                rule: rule.source.name,
                appId: serviceObject.appId,
                refCond: rule.source.refCondId,
                refValidator: rule.source.refValidator,
                refDataRange: rule.source.refDataRange
              };
              serviceObject.rules.push(ruleObject);
            });
            dirObject.children.push(serviceObject);
          }
          array.push(dirObject);
        }
        console.log(array);
        ApiPermissionDS.remove(appId).then(function () {
          ApiPermissionDS.batchInsert(array).then(function () {
            alert('success');
          });
        });

      }
    }
  }
</script>



