<template>
  <div>
    <Row>
      <Col span="6">
      <a @click="addNode()">+</a>
      <Tree :data="baseData" @on-select-change="editNode"></Tree>
      </Col>
      <Col span="18">
      <div v-show="showFormFlag">
        <Form :model="pageItem" :label-width="80">
          <Form-item label="Label">
            <Input v-model="pageItem.label" placeholder="请输入"></Input>
          </Form-item>
          <Form-item label="Path">
            <Input v-model="pageItem.path" placeholder="请输入"></Input>
          </Form-item>
          <Form-item label="templatePath">
            <Input v-model="pageItem.templatePath" placeholder="请输入"></Input>
          </Form-item>
          <Form-item label="description">
            <Input v-model="pageItem.description" placeholder="请输入"></Input>
          </Form-item>
          <Form-item>
            <Button type="primary" @click="saveNode">提交</Button>
          </Form-item>
        </Form>
      </div>
      </Col>
    </Row>
  </div>
</template>
<script type="text/ecmascript-6">

  import {PageDS} from './pageDs';

  var blankPageItem = {label: '', path: '', templatePath: '', description: '', pid: '', appId: ''};
  export default {
    created() {
      this.fetchData();
    },
    watch: {
      '$route': 'fetchData'
    },
    data () {
      return {
        pageItem: {},
        showFormFlag: false,
        baseData: [{
          expand: true,
          title: 'parent 1'
        }]
      }
    },
    methods: {
      fetchData(){
        var appId = this.$route.params.appId;
        var $this = this;
        PageDS.query(appId).then(function (data) {
          var treeData = [];
          data.forEach(function (item) {
            if (item.pid == undefined || item.pid == '') {
              item.expand = true;
              item.title = item.path;
              treeData.push(item);
            }
          })
          function appendChildren(array, pid, object) {
            array.forEach(function (item) {
              if (item.id == pid) {
                if (item.children == undefined) {
                  item.children = [];
                }
                item.children.push(object);
              } else if (item.children != undefined) {
                appendChildren(item.children, pid, object);
              }
            });
          }

          data.forEach(function (item) {
            if (item.pid == undefined || item.pid == '') {
            } else {
              appendChildren(treeData, item.pid, item)
            }
          })
          $this.baseData = treeData;
        });
      },
      addNode(pid) {
        var appId = this.$route.params.appId;
        var init = JSON.parse(JSON.stringify(blankPageItem));
        init.pid = pid;
        init.appId = appId;
        this.pageItem = init;
        this.showFormFlag = true;
      },
      editNode(nodes) {
        var init = JSON.parse(JSON.stringify(nodes[0]));
        this.pageItem = init;
        this.showFormFlag = true;
      },
      saveNode() {
        var $this = this;
        PageDS.save(this.pageItem).then(function () {
          $this.showFormFlag = false;
          $this.fetchData();
        });
      }
    }
  }
</script>



