<template>
  <div style="margin: auto; width: 100%;">
    <div class="layout">
      <div class="layout-ceiling">
        <div class="layout-ceiling-main">
          <a href="#">帮助中心</a> | <a href="#">管理应用</a>
        </div>
      </div>
    </div>
    <Row>
      <Col span="5">
      <Menu active-name="1" @on-select="clickMenu">
        <Menu-group :title="app.name" v-for="app in apps" :key="app.id">
          <Menu-item :name="app.id + '-1'">
            <Icon type="document-text"></Icon>
            Table
          </Menu-item>
          <Menu-item :name="app.id + '-2'">
            <Icon type="chatbubbles"></Icon>
            Entity
          </Menu-item>
          <Menu-item :name="app.id + '-3'">
            <Icon type="chatbubbles"></Icon>
            API
          </Menu-item>
          <Menu-item :name="app.id + '-4'">
            <Icon type="chatbubbles"></Icon>
            Page
          </Menu-item>
          <Menu-item :name="app.id + '-5'">
            <Icon type="chatbubbles"></Icon>
            API-Permission
          </Menu-item>
          <!--
          <Menu-item :name="app.id + '-6'">
            <Icon type="chatbubbles"></Icon>
            Page-Permission
          </Menu-item>
          -->
        </Menu-group>
      </Menu>
      </Col>
      <Col span="19" style="margin-top: 10px;margin-left: -40px;">
      <router-view></router-view>
      </Col>
    </Row>
  </div>
</template>

<script>

  import {DataSource} from '../../utils/dataSource';

  var ds = new DataSource({queryUrl: '/rest/configServer/access/appMgr'});

  export default {
    data () {
      return {
        apps: []
      }
    },
    mounted() {
      var $this = this;
      ds.paged().then(function (data) {
        return data.json();
      }).then(function (apps) {
        var list = apps.result;
        $this.apps = list;
      });
    },
    methods: {
      clickMenu: function (name) {
        var menu = name.split('-');
        var appId = menu[0];
        var op = menu[1];
        if (op == '1') {
          this.$router.push({path: '/table/' + appId})
        } else if (op == '2') {
          this.$router.push({path: '/entity/' + appId})
        } else if (op == '3') {
          this.$router.push({path: '/api/' + appId})
        } else if (op == '4') {
          this.$router.push({path: '/page/' + appId})
        } else if (op == '5') {
          this.$router.push({path: '/api-permission/' + appId})
        } else if (op == '6') {
          this.$router.push({path: '/page-permission/' + appId})
        }
//        console.log(name)
        //router
      }
    }
  }
</script>

<style>
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-header {
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
  }

  .layout-ceiling {
    background: #464c5b;
    padding: 10px 0;
    overflow: hidden;
  }

  .layout-ceiling-main {
    float: right;
    margin-right: 15px;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }
</style>



