import Vue from 'vue'
import iView from 'iview';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
Vue.use(iView);
import 'iview/dist/styles/iview.css';
import Config from './Config.vue';

import ConfigHome from './ConfigHome.vue';
import ConfigTable from './ConfigTable.vue';
import ConfigTableEdit from './ConfigTableEdit.vue';
import  ConfigEntity from './ConfigEntity.vue';
import ConfigEntityEdit from './ConfigEntityEdit.vue';
import  ConfigBundle from './ConfigBundle.vue';
import BundleEdit from './BundleEdit.vue';

import BundleTest from './BundleTest.vue';
import ConfigPage from './ConfigPage.vue';
import ConfigApiPermission from './ConfigApiPermission.vue';
import ConfigPagePermission from './ConfigPagePermission.vue';
const RouterConfig = {
  routes: [{
    path: '/',
    component: ConfigHome
  }, {
    path: '/table/:appId',
    component: ConfigTable
  }, {
    path: '/table/:appId/add',
    component: ConfigTableEdit
  }, {
    path: '/table/:appId/edit/:id',
    component: ConfigTableEdit
  }, {
    path: '/entity/:appId',
    component: ConfigEntity
  }, {
    path: '/entity/:appId/add',
    component: ConfigEntityEdit
  }, {
    path: '/entity/:appId/edit/:id',
    component: ConfigEntityEdit
  }, {
    path: '/api/:appId',
    component: ConfigBundle,
    children: [{
      path: 'add', component: BundleEdit
    }, {
      path: 'edit/:id', component: BundleEdit
    }, {
      path: 'test/:id/:ruleId', component: BundleTest
    }]
  }, {
    path: '/page/:appId',
    component: ConfigPage
  }, {
    path: '/api-permission/:appId',
    component: ConfigApiPermission
  }/*, {
   path: '/page-permission/:appId',
   component: ConfigPagePermission
   }*/]
};
const router = new VueRouter(RouterConfig);


/* eslint-disable no-new */
new Vue({
  el: '#config',
  router: router,
  components: {Config}
})



