<template>
  <Row>

    <Row>
      <Col span="4">
      <a @click="addBundleDir">+ dir</a>
      <Tree :data="baseData" @on-select-change="itemClick"></Tree>
      </Col>
      <Col span="20">
      <router-view></router-view>
      </Col>
    </Row>
  </Row>
</template>
<script type="text/ecmascript-6">
  import {DataSource} from '../../utils/dataSource';
  var ds = new DataSource({
    queryUrl: '/rest/configServer/access/bundle',
    deleteUrl: '/rest/configServer/access/entity'
  });

  export default {
    created() {
      this.fetchData();
    },
    watch: {
      '$route': 'fetchData'
    },
    data () {
      return {
        clickId: '',
        baseData: [{
          expand: true,
          title: 'parent 1',
          children: [{
            expand: true,
            title: 'parent 1',
            children: []
          }]
        }]
      }
    },
    methods: {
      itemClick(item) {
//        console.log(item);
        if (item[0].type == 'dir') {
          var appId = this.$route.params.appId;
          var path = "/api/" + appId + "/edit/" + item[0].id;
          this.clickId = item[0].id;
          this.$router.push({path: path})
        } else if (item[0].type == 'service') {
//          alert('service');
//          var appId = this.$route.params.appId;
//          var path = "/api/" + appId + "/test/" + item[0].id;
//          this.clickId = item[0].id;
//          this.$router.push({path: path})
        } else if (item[0].type == 'rule') {
//          alert('id');
//          serviceId
          var appId = this.$route.params.appId;
          var path = "/api/" + appId + "/test/" + item[0].serviceId + "/" + item[0].id;
          this.clickId = item[0].id;
          this.$router.push({path: path})
//          item[0].selected = true;
        }
      },
      addBundleDir() {
        var appId = this.$route.params.appId;
        var path = '/api/' + appId + '/add';
        this.$router.push({path: path})
      },
      fetchData(){
        var $this = this;
        var appId = this.$route.params.appId;
        var ruleId = this.$route.params.ruleId;
        ds.paged({
          appId: appId
        }).then(function (data) {
          return data.json()
        }).then(function (data) {
          console.log(data.result);
          var bundles = [];

          function append(pid, root, value) {
            for (var i = 0; i < root.length; i++) {
              if (root[i].id == pid) {
                if (root[i].children == undefined) {
                  root[i].children = [];
                }
                root[i].children.push(value);
                break;
              }
              if (root.children != undefined) {
                append(pid, root.children, value);
              }
            }
          }

          function convertObject(o) {
            var items = [];
            if (o.rules) {
              o.rules.forEach(function (element) {
                var title = "<span>" + element.name + "&nbsp;<i class='ivu-icon ivu-icon-android-send' style='color: #39f'></i></span>";
                var object = {
                  serviceId: o.id,
                  id: element.id,
                  expand: true,
                  title: title,
                  type: 'rule',
                  selected: false
                }
                if (ruleId && ruleId == object.id) {
                  object.selected = true;
                }
                items.push(object);
              });
            }
            var title = o.name + ":" + o.method;
            var expand = true;
            //<Icon type="folder"></Icon>
            //<Icon type="ios-folder-outline"></Icon>
            //<Icon type="android-send"></Icon>
            if (o.type == 'dir') {
              //<i class="ivu-icon ivu-icon-document-text">
              title = "<span><i class='ivu-icon ivu-icon-ios-folder-outline'></i>&nbsp" + o.name + "</span>";
            } else if (o.type == 'service') {
              title = "<span> &nbsp;<i class='ivu-icon ivu-icon-steam'></i> " + o.name + ":" + o.method + "&nbsp;&nbsp;</span>";
              expand = true;
            }
            return {
              id: o.id,
              expand: expand,
              title: title,
              type: o.type,
              children: items
            }
          }

          for (var i = 0; i < data.result.length; i++) {
            var value = data.result[i];
            if (value.pid == undefined) {
              bundles.push(convertObject(value))
            }
          }
          for (var i = 0; i < data.result.length; i++) {
//            var type = data.result[i].type;
            var value = data.result[i];
            if (value.pid == undefined) {
//              bundles.push(convertObject(value))
            } else {
              append(value.pid, bundles, convertObject(value))
            }
          }

          $this.baseData = bundles;

        })
      }
    }
  }
</script>



