<template>
  <div style="width: 85%; margin: auto">
    <Form :model="entityItem" :label-width="80 ">
      <Row>
        <Col span="12">
        <Form-item label="name">
          <Input v-model="entityItem.name" placeholder="name"></Input>
        </Form-item>
        </Col>
      </Row>
      <Row>
        <Col span="12">
        <Form-item label="mainTableId">
          <Select v-model="entityItem.mainTableId">
            <Option v-for="item in tableList" :value="item.value" :key="item">{{ item.label }}</Option>
          </Select>
        </Form-item>
        </Col>
        <Col span="12">
        <Form-item label="mainAlias">
          <Input v-model="entityItem.mainAlias" placeholder="mainAlias"></Input>
        </Form-item>
        </Col>
      </Row>
      <Row>
        <Col span="12">
        <Form-item label="label">
          <Input v-model="entityItem.label" placeholder="label"></Input>
        </Form-item>
        </Col>
        <Col span="12">
        <Form-item label="mainCondition">
          <Input v-model="entityItem.mainCondition" placeholder="mainCondition"></Input>
        </Form-item>
        </Col>
      </Row>
    </Form>

    <Row>
      <Col span="24">
      <p><b>Relationship <a @click="addRelation"> + </a></b></p>
      <Table :columns="relationColumns" :data="relationList"></Table>
      </Col>
    </Row>

    <Row>
      <Col span="24">
      <p><b>Conditions <a @click="addCondition"> + </a></b></p>
      <Table :columns="conditionColumns" :data="conditionList"></Table>
      </Col>
    </Row>

    <div style="margin-top: 15px">
      <Row>
        <Col span="8">
        &nbsp;
        </Col>
        <Col span="8">
        <Button type="primary" long icon="plus-round" @click="saveEntity">Save</Button>
        </Col>
        <Col span="8">

        </Col>
      </Row>
    </div>

    <Modal
      v-model="relationModel"
      title="relation"
      @on-ok="saveRelation"
    >

      <Form :model="relationItem" :label-width="80 ">
        <Row>
          <Col span="12">
          <Form-item label="alias">
            <Input v-model="relationItem.alias" placeholder="alias"></Input>
          </Form-item>
          </Col>
          <Col span="12">
          <Form-item label="refObjectId">
            <Select v-model="relationItem.refObjectId">
              <Option v-for="item in tableList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
        </Row>

        <Row>
          <Col span="12">
          <Form-item label="type">
            <Select v-model="relationItem.type">
              <Option v-for="item in entityRelTypeList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
          <Col span="12">
          <Form-item label="condition">
            <Input v-model="relationItem.condition" placeholder="condition"></Input>
          </Form-item>
          </Col>
        </Row>

        <Row>
          <Col span="12">
          <Form-item label="sourceId">
            <Input v-model="relationItem.sourceId" placeholder="sourceId"></Input>
          </Form-item>
          </Col>
          <Col span="12">
          <Form-item label="targetId">
            <Input v-model="relationItem.targetId" placeholder="targetId"></Input>
          </Form-item>
          </Col>
        </Row>
      </Form>
    </Modal>


    <Modal
      v-model="conditionModel"
      title="condition"
      @on-ok="saveCondition"
    >
      <Form :model="conditionItem" :label-width="80 ">
        <Row>
          <Col span="24">
          <Form-item label="name">
            <Input v-model="conditionItem.name" placeholder="name"></Input>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="24">
          <Form-item label="expression">
            <Input v-model="conditionItem.expression" placeholder="expression"></Input>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="6">
          <Form-item label="paged">
            <Checkbox label="" v-model="conditionItem.paged"></Checkbox>
          </Form-item>
          </Col>
          <Col span="6">
          <Form-item label="fetchOne">
            <Checkbox label="" v-model="conditionItem.fetchOne"></Checkbox>
          </Form-item>
          </Col>
          <Col span="6">
          <Form-item label="withChildren">
            <Checkbox label="" v-model="conditionItem.withChildren"></Checkbox>
          </Form-item>
          </Col>
          <Col span="6">
          <Form-item label="auto">
            <Checkbox label="" v-model="conditionItem.auto"></Checkbox>
          </Form-item>
          </Col>
          <Col span="6">
          <Form-item label="count">
            <Checkbox label="" v-model="conditionItem.count"></Checkbox>
          </Form-item>
          </Col>
        </Row>

      </Form>
    </Modal>
  </div>
</template>
<script type="text/ecmascript-6">

  import {EntityDS} from './entityDs';

  import {DS} from './tableDs';

  var initEntity = {mainTableId: '', mainAlias: '', label: '', mainCondition: ''};

  var initRelation = {
    alias: '',
    refObjectId: '',
    type: '',
    sourceId: '',
    targetId: '',
    condition: '',
    name: ''
  };
  var initCondition = {
    name: '',
    expression: '',
    paged: false,
    fetchOne: false,
    withChildren: false,
    auto: false,
    count: false,
  }

  export default {
    data () {
      return {
        conditionModel: false,
        relationModel: false,
        relationItem: {},
        conditionItem: {},
        entityItem: JSON.parse(JSON.stringify(initEntity)),
        entityRelTypeList: [
          {value: 'ref', label: 'one2many'},
          {value: 'child', label: 'many2one'}
        ],
        tableList: [],
        relationColumns: [
          {
            title: 'alias',
            key: 'alias'
          },
          {
            title: 'refObjectId',
            key: 'refObjectIdLabel'
          },
          {
            title: 'type',
            key: 'type'
          },
          {
            title: 'sourceId',
            key: 'sourceId'
          },
          {
            title: 'targetId',
            key: 'targetId'
          },
          {
            title: 'condition',
            key: 'condition'
          }, {
            title: 'OP',
            key: 'action',
            width: 130,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {},
                  on: {
                    click: () => {
                      this.editEditRelation(params.index)
                    }
                  }
                }, 'edit'),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {},
                  on: {
                    click: () => {
                      this.removeRelation(params.index)
                    }
                  }
                }, 'delete')
              ]);
            }
          }
        ],
        relationList: [],
        relationRemoveList: [],
        conditionColumns: [
          {
            title: 'name',
            key: 'name'
          },
          {
            title: 'expression',
            key: 'expression'
          },
          {
            title: 'paged',
            key: 'paged'
          },
          {
            title: 'fetchOne',
            key: 'fetchOne'
          },
          {
            title: 'withChildren',
            key: 'withChildren'
          },
          {
            title: 'auto',
            key: 'auto'
          }, {
            title: 'OP',
            key: 'action',
            width: 130,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {},
                  on: {
                    click: () => {
                      this.editCondition(params.index)
                    }
                  }
                }, 'edit'),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {},
                  on: {
                    click: () => {
                      this.removeCondition(params.index)
                    }
                  }
                }, 'delete')
              ]);
            }
          }
        ],
        conditionList: [],
        removeConditionList: []
      }
    },
    methods: {
      _findTableLabel(id) {
        var vv = '';
        this.tableList.forEach(function (e) {
          if (e.value == id) {
            vv = e.label;
          }
        });
        return vv;
      },
      _resovleRelation(relValue) {
        return relValue['refObjectIdLabel'] = this._findTableLabel(relValue.refObjectId);
      },
      removeRelation(index) {
        var r = this.relationList.splice(index, 1);
        for (var i = 0; i < r.length; i++) {
          if (r[i].id != undefined) {
            r[i]._status_ = 'delete';
            this.relationRemoveList.push(r[i]);
          }
        }
      },
      editEditRelation(index) {
        var item = this.relationList[index];
        this.relationItem = JSON.parse(JSON.stringify(item));
        this.relationItem.editIndex = index;
        this.relationModel = true;
      },
      saveRelation() {
        var relValue = JSON.parse(JSON.stringify(this.relationItem));
        this._resovleRelation(relValue);
        //refObjectId
        if (relValue.editIndex >= 0) {
          this.relationList.splice(relValue.editIndex, 1, relValue);
        } else {
          this.relationList.push(relValue);
        }
      },
      addRelation() {
        this.relationItem = JSON.parse(JSON.stringify(initRelation));
        this.relationItem.editIndex = -1;
        this.relationModel = true;
      },
      editCondition(index) {
        this.conditionItem = JSON.parse(JSON.stringify(this.conditionList[index]));
        this.conditionItem.editIndex = index;
        this.conditionModel = true;
      },
      addCondition() {
        this.conditionItem = JSON.parse(JSON.stringify(initCondition));
        this.conditionItem.editIndex = -1;
        this.conditionModel = true;
      },
      saveCondition() {
        var relValue = JSON.parse(JSON.stringify(this.conditionItem));
        if (relValue.editIndex >= 0) {
          this.conditionList.splice(relValue.editIndex, 1, relValue);
        } else {
          this.conditionList.push(relValue);
        }
      },
      removeCondition(index){
        var c = this.conditionList.splice(index, 1);
        for (var i = 0; i < c.length; i++) {
          if (c[i].id != undefined) {
            c[i]._status_ = 'delete';
            this.removeConditionList.push(c[i]);
          }
        }
      },
      handleAddColumn(){
        var item = JSON.parse(JSON.stringify(this.columnItem));
        this.columnList.push(item);
        this.columnItem = JSON.parse(JSON.stringify(initColumnItem));
      },
      saveEntity() {
        var $this = this;
        var appId = this.$route.params.appId;
        var entityId = this.$route.params.id;
        var entity = JSON.parse(JSON.stringify(this.entityItem));
        if (entityId) {
          entity.id = entityId;
        }
        entity.appId = appId;
        var relation = JSON.parse(JSON.stringify(this.relationList));
        relation.forEach(function (o) {
          o.appId = appId;
        });
        if (this.relationRemoveList) {
          for (var i = 0; i < this.relationRemoveList.length; i++) {
            relation.push(this.relationRemoveList[i]);
          }
        }
        entity.rels = relation;

        var conditions = JSON.parse(JSON.stringify(this.conditionList));
        conditions.forEach(function (o) {
          o.appId = appId;
        });
        if (this.removeConditionList) {
          for (var i = 0; i < this.removeConditionList.length; i++) {
            conditions.push(this.removeConditionList[i]);
          }
        }
        entity.conds = conditions;

        EntityDS.save(entity).then(function () {
          $this.$router.go(-1);
        });
      },
      fetchData() {
        var $this = this;
        var appId = this.$route.params.appId;
        DS.query(appId).then(function (data) {
          var array = [];
          data.forEach(function (o) {
            array.push({value: o.id, label: o.label})
          });
          $this.tableList = array;
        });

        var entityId = this.$route.params.id;

        if (entityId) {
          EntityDS.findById(entityId).then(function (data) {
            $this.entityItem = data;
            if (data.rels) {
              data.rels.forEach(function (d) {
                d._status_ = '';
              });
              $this.relationList = data.rels;
            }
            if (data.conds) {
              data.conds.forEach(function (d) {
                d._status_ = '';
              });
              $this.conditionList = data.conds;
            }
          });
        }
      }
    },
    created() {
      this.fetchData();
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': 'fetchData'
    },
    computed: {}
  }
</script>



