<template>
  <div>
    <div style="margin-bottom: 5px">
      <Button type="primary" icon="plus-round" @click="addTable">add</Button>
    </div>
    <Table border :columns="tableColumns" :data="tables"></Table>

  </div>
</template>
<script type="text/ecmascript-6">
  import {DataSource} from '../../utils/dataSource';
  var ds = new DataSource({queryUrl: '/rest/configServer/access/table', deleteUrl: '/rest/configServer/access/table'});

  export default {
    created() {
      this.fetchData();
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': 'fetchData'
    },
    data () {
      return {
        tableColumns: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: 'id',
            key: 'id'
          }, {
            title: 'tableId',
            key: 'tableId'
          },
          {
            title: 'label',
            key: 'label'
          }, {
            title: 'tableName',
            key: 'tableName'
          }, {
            title: '操作',
            key: 'action',
//            width: 120,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.editTable(params.index)
                    }
                  }
                }, 'edit'),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.removeTable(params.index)
                    }
                  }
                }, 'delete')
              ]);
            }
          }
        ],
        tables: []
      }
    },
    methods: {
      removeTable(index) {
        var $this = this ;
        var id = this.tables[index].id;
        this.$Modal.confirm({
          title: 'delete?',
          content: 'sure ?',
          onOk: () => {
            ds.remove({id: id}).then(function (data) {
              return data.json();
            }).then(function (data) {
              $this.fetchData();
            });
          },
          onCancel: () => {

          }
        });
      },
      editTable(index) {
        var path = "/table/" + this.$route.params.appId + "/edit/" + this.tables[index].id;
        this.$router.push({path: path})
      },
      addTable() {
        var path = "/table/" + this.$route.params.appId + "/add";
        this.$router.push({path: path})
      },
      fetchData()
      {
        var $this = this;
        var appId = this.$route.params.appId;
        ds.paged({
          appId: appId
        }).then(function (data) {
          return data.json()
        }).then(function (data) {
          $this.tables = data.result;
        })
      }
    }
  }
</script>



