<template>
  <div style="border-left: 1px solid #ccc; min-height: 400px; padding: 5px">
    <Form :model="bundleItem" :label-width="80 ">
      <Row>
        <Col span="12">
        <Form-item label="Dir name">
          <Input v-model="bundleItem.name" placeholder="name"></Input>
        </Form-item>
        </Col>
      </Row>
    </Form>
    <p><b>Service <a @click="addService">+</a> </b></p>
    <table class="ivu-table ivu-table-border" style="width: 100%">
      <tr class="ivu-table-row">
        <th width="100">
          <div class="ivu-table-cell">name</div>
        </th>
        <th width="100">
          <div class="ivu-table-cell">service</div>
        </th>

        <th>
          <div class="ivu-table-cell">Rule
          </div>
        </th>
        <th width="170">
          <div class="ivu-table-cell">OP</div>
        </th>
      </tr>

      <template v-for="(item, index) in services">
        <tr>
          <td>
            <div class="ivu-table-cell">
              {{item.service}}
            </div>
          </td>
          <td>
            <div class="ivu-table-cell">
              {{item.entityName}} - ({{item.method}})
            </div>
          </td>
          <td>
            <table class="ivu-table ivu-table-border" style="width: 100%">
              <tr v-for="(rule, i) in item.rules">
                <td>
                  <div class="ivu-table-cell">{{rule.rule}}</div>
                </td>
                <td>
                  <div class="ivu-table-cell">{{rule.refCondDisplay}}</div>
                </td>
                <td>
                  <div class="ivu-table-cell">{{rule.refValidator}}</div>
                </td>
                <td>
                  <div class="ivu-table-cell">{{rule.refDataRange}}</div>
                </td>
                <td>
                  <div class="ivu-table-cell">
                    <a @click="removeRule(index, i)"> X </a>
                  </div>
                </td>
              </tr>
            </table>
          </td>
          <td>
            <div class="ivu-table-cell">
              <a @click="addRule(item.entityId, index)">add rule</a> |
              <a @click="updateService(item,index)">update</a> |
              <a @click="removeService(index)">delete</a>
            </div>
          </td>
        </tr>

      </template>
    </table>

    <div style="margin-top: 15px">
      <Row>
        <Col span="8">
        &nbsp;
        </Col>
        <Col span="8">
        <Button type="primary" long icon="plus-round" @click="saveBundle">Save</Button>
        </Col>
        <Col span="4">
        &nbsp;
        </Col>
        <Col span="4">
        <Button type="ghost" @click="deleteBundle" v-show="showDeleteButton">delete this dir</Button>
        </Col>
      </Row>
    </div>

    <Modal
      v-model="serviceModel"
      title="service"
      @on-ok="saveService"
    >
      <Form :model="serviceItem" :label-width="80">
        <Row>
          <Col span="24">
          <Form-item label="name">
            <Input v-model="serviceItem.service" placeholder="name"></Input>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="24">
          <Form-item label="Ref Entity">
            <Select v-model="serviceItem.entityId">
              <Option v-for="item in entityList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="24">
          <Form-item label="http method">
            <Select v-model="serviceItem.method">
              <Option v-for="item in methodList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
        </Row>
      </Form>
    </Modal>

    <Modal
      v-model="ruleModel"
      title="rule"
      @on-ok="saveRule"
    >
      <Form :model="ruleItem" :label-width="80">
        <Row>
          <Col span="24">
          <Form-item label="name">
            <Input v-model="ruleItem.name" placeholder="name"></Input>
          </Form-item>
          </Col>
        </Row>

        <Row>
          <Col span="24">
          <Form-item label="ref condition">
            <Select v-model="ruleItem.refCondition">
              <Option v-for="item in refConditionList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
        </Row>
      </Form>
    </Modal>


  </div>
</template>
<script type="text/ecmascript-6">
  import {DataSource} from '../../utils/dataSource';

  import {BundleDS} from './bundleDs';

  var ds = new DataSource({
    queryUrl: '/rest/configServer/access/entity',
    deleteUrl: '/rest/configServer/access/entity',

  });



  export default {
    created() {
      this.fetchData();
    },
    watch: {
      '$route': 'fetchData'
    },
    computed: {
      showDeleteButton: function () {
        var id = this.$route.params.id;
        return id != undefined;
      }
    },
    data () {
      return {
        ruleModel: false,
        ruleItem: {},
        methodList: [{
          value: 'get',
          label: 'get'
        }, {
          value: 'post',
          label: 'post'
        }, {
          value: 'put',
          label: 'put'
        }, {
          value: 'delete',
          label: 'delete'
        }],
        serviceModel: false,
        serviceItem: {},
        bundleItem: {
          name: ''
        },
        services: [/*{
         entityId: "804419796677431296",
         entityName: "a",
         method: "get",
         service: "w",
         rules: []
         }*/],
        removeServices: [],
        entityList: [],
        refConditionMap: {},
        refConditionList: []
      }
    },
    methods: {
      deleteBundle() {
        var $this = this;
        var appId = this.$route.params.appId;
        var id = this.$route.params.id;
        BundleDS.deleteDir(id).then(function () {
          var path = "/api/" + appId;
          $this.$router.push({path: path})
        });
      },
      saveBundle() {
        // this.bundle
        // services:[]
        //
        var $this = this;
        var appId = this.$route.params.appId;
        var bundleId = this.$route.params.id;
        var name = this.bundleItem.name;

        var type = 'dir';
        var items = [];
        if (this.services) {
          for (var i = 0; i < this.services.length; i++) {
            var service = this.services[i];
            var rules = [];
            if (service.rules) {
              for (var j = 0; j < service.rules.length; j++) {
                var r = service.rules[j];
                var o = {
                  id: r.id,
                  serviceId: service.id,
                  name: r.rule,
                  refCondId: r.refCond
                }
                rules.push(o);
              }
            }
            if (service.deleteRules) {
              service.deleteRules.forEach(function (o) {
                rules.push(o);
              });
            }
            var o = {
              id: service.id,
              pid: bundleId,
              appId: appId,
              entityId: service.entityId,
              name: service.service,
              method: service.method,
              type: 'service',
              rules: rules
            }
            items.push(o);
          }
        }
        this.removeServices.forEach(function (o) {
          o._status_ = 'delete';
          items.push(o);
        });

        var dir = {
          id: bundleId,
          appId: appId,
          name: name,
          type: type,
          items: items
        }

        BundleDS.save(dir).then(function () {
          var path = "/api/" + appId;
          $this.$router.push({path: path})
        });


      },
      _refConditionList(entityId) {
        this.refConditionList = [];
        this.refConditionList = JSON.parse(JSON.stringify(this.refConditionMap[entityId]))
      },
      removeRule(serviceIndex, index) {
        var service = this.services[serviceIndex];
        var removeArray = service.rules.splice(index, 1);
        if (removeArray[0].id != undefined) {
          if (service.deleteRules == undefined) {
            service.deleteRules = [];
          }
          removeArray[0]._status_ = 'delete';
          service.deleteRules.push(removeArray[0]);
        }
      },
      _findRefConditionLabel(value) {
//        debugger;
        for (var i = 0; i < this.refConditionList.length; i++) {
          if (this.refConditionList[i].value == value) {
            return this.refConditionList[i].label;
          }
        }
        return null;
      },

      saveRule() {
        //rule, refCond
        var serviceIndex = this.ruleItem.serviceIndex;

        var o = {
          rule: this.ruleItem.name,
          refCond: this.ruleItem.refCondition,
          refCondDisplay: this._findRefConditionLabel(this.ruleItem.refCondition)
        };

        this.services[serviceIndex].rules.push(o);
      },
      addRule(entityId, serviceIndex) {
        this.ruleItem = {name: '', refCondition: '', _index: -1, serviceIndex: serviceIndex};
        this._refConditionList(entityId);
        this.ruleModel = true;
      },
      fetchData() {
        //entityList
        var $this = this;
        var appId = this.$route.params.appId;
//        var id = this.$route.params.id;
//        debugger;
        ds.paged({
          appId: appId
        }).then(function (data) {
          return data.json()
        }).then(function (data) {
          var array = [];
          data.result.forEach(function (element) {
            array.push({value: element.id, label: element.label})
            var condArray = [];
            if (element.conds) {
              element.conds.forEach(function (o) {
                condArray.push({value: o.id, label: o.name + "-" + o.expression + "-" + o.auto})
              });
            }
            $this.refConditionMap[element.id] = condArray;
          });
          $this.entityList = array;
          var id = $this.$route.params.id;
          if (id != undefined) {
//            debugger;
            BundleDS.findById(id).then(function (data) {
              $this.bundleItem.name = data.name;
              if (data.items) {
                data.items.forEach(function (o) {
                  o.service = o.name;
                  if (o.rules) {
                    o.rules.forEach(function (rule) {
                      rule.rule = rule.name;
                      rule.refCondDisplay = rule.refCondId$display$;
                      rule.refCond = rule.refCondId;
                    })
                  }
                })
              }
              $this.services = data.items == undefined ? [] : data.items;

              $this.services.forEach(function (o) {
                if (o.rules == undefined) {
                  o.rules = [];
                }
              });

              console.log(data);
            });
          } else {
            $this.bundleItem.name = '';
            $this.services = [];
            $this.serviceItem = {};
          }
        })
      },
      _getEntityLabel(eid){
        for (var i = 0; i < this.entityList.length; i++) {
          if (this.entityList[i].value == eid) {
            return this.entityList[i].label;
          }
        }
        return null;
      },
      addService() {
        this.serviceItem = {service: '', method: '', entityId: '', _index: -1};
        this.serviceModel = true;
      },
      updateService(item, index){
        this.serviceItem = {
          id: item.id,
          service: item.service,
          entityId: item.entityId,
          entityName: this._getEntityLabel(item.entityId),
          method: item.method,
          rules: item.rules,
          _index: index
        };
        this.serviceModel = true;
      },
      removeService(index) {
        var removedItem = this.services.splice(index, 1);
        for (var i = 0; i < removedItem.length; i++) {
          if (removedItem[i].id != undefined) {
            this.removeServices.push(removedItem[i]);
          }
        }
      },
      saveService() {
        var service = this.serviceItem.service;
        var method = this.serviceItem.method;
        var entityId = this.serviceItem.entityId;
        var entityName = this._getEntityLabel(this.serviceItem.entityId);
        var value = {
          service: service,
          method: method,
          entityId: entityId,
          entityName: entityName
        };
        if (this.serviceItem._index >= 0) {
          this.services[this.serviceItem._index].service = service;
          this.services[this.serviceItem._index].method = method;
          this.services[this.serviceItem._index].entityId = entityId;
          this.services[this.serviceItem._index].entityName = entityName;
//          debugger;
//          var removedItem = this.services.splice(this.serviceItem._index, 1, value);
//          for (var i = 0; i < removedItem.length; i++) {
//            if (removedItem[i].id != undefined) {
//              this.removeServices.push(removedItem[i]);
//            }
//          }
        } else {
          this.services.push(value)
        }
      }
    }
  }
</script>
<style>

</style>



