import {DataSource} from '../../utils/dataSource';

var queryURL = '/rest/configServer/access/entity';
var ds = new DataSource({
  queryUrl: queryURL,
  createUrl: '/rest/configServer/access/entity',
  updateUrl: '/rest/configServer/access/entity',
  name: 'entityEntity'
});




var EntityDS = {
  save(value) {
    if (value.id) {
      return ds.update(value, {id: value.id}).then(function (data) {
        return data.json()
      });
    } else {
      return ds.create(value).then(function (data) {
        return data.json()
      });
    }
  },
  findById(id) {
    return ds.query(queryURL, {id: id}).then(function (data) {
      return data.json();
    }).then(function (data) {
      return data.result[0]
    })
  }
}


export {EntityDS}



