import {DataSource} from '../../utils/dataSource';

var queryURL = '/rest/configServer/access/bundle';
var ds = new DataSource({
  queryUrl: queryURL,
  createUrl: '/rest/configServer/access/bundle',
  updateUrl: '/rest/configServer/access/bundle',
  deleteUrl: '/rest/configServer/access/bundle',
  name: 'entityServiceEntity'
});


var BundleDS = {
  save(value) {
    if (value.id) {
      return ds.update(value, {id: value.id}).then(function (data) {
        return data.json()
      });
    } else {
      return ds.create(value).then(function (data) {
        return data.json()
      });
    }
  },
  findById(id) {
    return ds.query(queryURL, {id: id}).then(function (data) {
      return data.json();
    }).then(function (data) {
      return data.result[0]
    })
  },
  deleteDir(id) {
    return ds.remove({id: id}).then(function (data) {
      return data.json();
    }).then(function (data) {
      return data;
    });
  }
}


export {BundleDS}



