<template>
  <div style="width: 80%; margin: auto">
    <Form ref="tableForm" :model="tableItem" :label-width="80" :rules="ruleTableItem">
      <Row>
        <Col span="12">
        <Form-item label="tableId">
          <Input v-model="tableItem.tableId" placeholder="tableId"></Input>
        </Form-item>
        </Col>
        <Col span="12">
        <Form-item label="tableName">
          <Input v-model="tableItem.tableName" placeholder="tableName"></Input>
        </Form-item>
        </Col>
      </Row>
      <Row>
        <Col span="12">
        <Form-item label="label">
          <Input v-model="tableItem.label" placeholder="label"></Input>
        </Form-item>
        </Col>
        <Col span="3">
        <Form-item label="view">
          <Checkbox label="" v-model="tableItem.view"></Checkbox>
        </Form-item>
        </Col>
        <Col span="3">
        <Form-item label="virtual">
          <Checkbox label="" v-model="tableItem.virtual"></Checkbox>
        </Form-item>
        </Col>
      </Row>
    </Form>


    <Row>
      <Col span="24">
      <p><b>Columns </b></p>
      <Table :columns="columnColumns" :data="columnList"></Table>
      </Col>
    </Row>
    <div style="margin-top: 30px; border: 1px dotted #ccc; padding: 5px">
      <Form :model="columnItem" :label-width="90 ">
        <Row>
          <Col span="12">
          <Form-item label="Name">
            <Input v-model="columnItem.name" placeholder="name"></Input>
          </Form-item>
          </Col>

          <Col span="12">
          <Form-item label="ColumnName">
            <Input v-model="columnItem.columnName" placeholder="columnName"></Input>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="12">
          <Form-item label="ColumnType">
            <Select v-model="columnItem.columnType">
              <Option v-for="item in columnTypeList" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          </Col>
          <Col span="12">
          <Form-item label="Label">
            <Input v-model="columnItem.label" placeholder="label"></Input>
          </Form-item>
          </Col>
        </Row>
        <Row>
          <Col span="6">
          <Form-item label="Length">
            <Input v-model="columnItem.length" placeholder="length"></Input>
          </Form-item>
          </Col>
          <Col span="3">
          <Form-item label="unique">
            <Checkbox label="" v-model="columnItem.unique"></Checkbox>
          </Form-item>
          </Col>
          <Col span="4">
          <Form-item label="nullable">
            <Checkbox label="" v-model="columnItem.nullable"></Checkbox>
          </Form-item>
          </Col>
          <Col span="6">
          <Button type="dashed" long @click="handleAddColumn" icon="plus-round">Save</Button>
          </Col>
        </Row>
      </Form>
    </div>
    <div style="margin-top: 15px">
      <Row>
        <Col span="8">
        &nbsp;
        </Col>
        <Col span="8">
        <Button type="primary" long icon="plus-round" @click="saveTable('tableForm')">Save</Button>
        </Col>
        <Col span="8">

        </Col>
      </Row>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">

  import {DS} from './tableDs';

  var initTable = {tableId: '', tableName: '', label: '', view: false, virtual: false};

  var initColumn = [{
    name: 'id',
    label: 'Id',
    columnType: 'LONG'
  }]

  var initColumnItem = {
    name: '',
    columnName: '',
    columnType: 'STRING',
    label: '',
    length: '',
    unique: false,
    nullable: false
  };

  export default {
    data () {
      return {
        ruleTableItem: {
          tableId: [{required: true}]
        },
        tableItem: JSON.parse(JSON.stringify(initTable)),
        columnItem: JSON.parse(JSON.stringify(initColumnItem)),
        columnTypeList: [
          {value: 'STRING', label: 'STRING'},
          {value: 'INTEGER', label: 'INTEGER'},
          {value: 'LONG', label: 'LONG'},
          {value: 'BOOLEAN', label: 'BOOLEAN'},
          {value: 'BIG_DECIMAL', label: 'BIG_DECIMAL'},
          {value: 'TIMESTAMP', label: 'TIMESTAMP'},
          {value: 'LONG_TEXT', label: 'LONG_TEXT'},
          {value: 'BLOB', label: 'BLOB'}
        ],
        deleteColumns: [],
        columnColumns: [
          {
            title: 'Label',
            key: 'label'
          },
          {
            title: 'Name',
            key: 'name'
          },
          {
            title: 'ColumnType',
            key: 'columnType'
          }, {
            title: '操作',
            key: 'action',
            width: 60,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.removeColumn(params.index)
                    }
                  }
                }, 'X')
              ]);
            }
          }
        ],
        columnList: initColumn
      }
    },
    methods: {
      handleAddColumn(){
        var item = JSON.parse(JSON.stringify(this.columnItem));
        this.columnList.push(item);
        this.columnItem = JSON.parse(JSON.stringify(initColumnItem));
      },
      removeColumn(index) {
        if (this.columnList[index].name == 'id') {
          alert("id can't delete");
        } else {
          var remove = this.columnList.splice(index, 1);
          for (var i = 0; i < remove.length; i++) {
            if (remove[i].id != undefined) {
              remove[i]._status_ = 'delete';
              this.deleteColumns.push(remove[i]);
            }
          }
        }
      },
      saveTable(name) {
        debugger;
        var $this = this;
        var appId = this.$route.params.appId;
        var tableId = this.$route.params.id;
        var table = JSON.parse(JSON.stringify(this.tableItem));
        if (tableId) {
          table.id = tableId;
        }
        table.pkColumn = "id";
        table.appId = appId;
        var column = JSON.parse(JSON.stringify(this.columnList));
        column.forEach(function (o) {
          o.appId = appId;
        });
        if (this.deleteColumns) {
          for (var i = 0; i < this.deleteColumns.length; i++) {
            column.push(this.deleteColumns[i]);
          }
        }
        table.columns = column;
        this.$refs[name].validate((valid) => {
          debugger;
          console.log(valid);
          if (valid) {
            DS.save(table).then(function () {
              $this.$router.go(-1);
            });
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      fetchData() {
        var tableId = this.$route.params.id;
        var $this = this;
        if (tableId) {
          DS.findById(tableId).then(function (data) {
            $this.tableItem = data;
            data.columns.forEach(function (d) {
              d._status_ = '';
            });
            $this.columnList = data.columns;
          });
        }
      }
    },
    created() {
      this.fetchData();
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': 'fetchData'
    },
    computed: {}
  }
</script>



